import './EntityList.css';
import * as React from "react";
import {returnToScanner} from "../components/Dialog";

export default class EntityList extends React.Component {
    render() {
        //Sort alphabetically
        let entities = this.props.entities.slice();
        entities.sort((a, b) => a.name.localeCompare(b.name));
        let planets = entities.map((entity) => {
            return <button className="planet-link" key={entity.id} onClick={() => this.props.onSelectedEntity(entity.id)}>
                <img src={entity.image} alt={entity.name} /> {entity.name}
            </button>;
        });

        return <div className="dialog">
            <div className="dialog-inner">
                <div className="dialog-content">
                    <div className="planet-list">
                        <h1>Select a system:</h1>
                        {planets}
                    </div>
                </div>
                <div className="dialog-bottom">
                    <button className="not-important" onClick={() => returnToScanner()}>Back</button>
                </div>
            </div>
        </div>
    }
}
